.wrap-layer{
	background: url(../img/layerBg.png);
	background-size: 100% 100%;
	width: 28.15rem;
	height: 30.8rem;
	position: absolute;
	top: 2.5rem;
	left: 50%;
	margin-left: -14.075rem;
	z-index: 300;
	.close{
		background: url(../img/close-icon.png);
		background-size: 100% 100%;
		width: 0.575rem;
		height: 0.575rem;
		position: absolute;
		top:0.5rem;
		right: 0.8rem;
		cursor: pointer;
	}
	.layer-info{
		padding:0 0.5rem 0 1.15rem;
		overflow: hidden;
		i,span{
			display: inline-block;
			vertical-align: middle;
		}
		.title{
			background: url(../img/title-bg.png);
			background-size: 100% 100%;
			width: 7.5rem;
			height: 1.0rem;
			line-height: 1.0rem;
			text-align: center;
			font-size: 0.5rem;
			font-weight: bold;
			color: #fff;
			position: absolute;
			top: -0.2rem;
			left: 50%;
			margin-left: -3.75rem;
		}
		.info{
			padding: 0.4rem 0.65rem 0 0;
			margin-top: 1.2rem;
			height: 28.75rem;
			overflow: hidden;
		}
		.piece{
			position: relative;
			border: 1px solid #123e8d;
			.tel{
				position: absolute;
				top: -0.5rem;
				left: 0.5rem;
				border: 1px solid #3165bc;
				height: 0.75rem;
				line-height: 0.65rem;
				background: #000521;
				i{
					display: inline-block;
					width: 3px;
					height: 0.45rem;
					background: #3165bc;
					margin: 0 0.175rem;
				}
				span{
					padding-right: 0.3rem;
					color: #a3c7ff;
					font-weight: bold;
				}
			}
			.table-info{
				padding:0.7rem 0.3rem 0;
				table{
					width: 100%;
				}
				td{
					font-size: 0.45rem;
					padding: 0.225rem;
				}
				.c-bacbe6{
					color: #bacbe6
				}
				.c-fff{
					color: #fff;
				}
				.w30{
					width: 30%;
				}
				.w40{
					width: 39.9%;
				}
				.jg{
					height: 1.0rem;
					line-height: 1.0rem;
					padding:0 0.225rem;
					border-top: 1px dashed #123e8d;
					.l,.m{
						display: inline-block;
					}
					.l{
						width: 30%;
					}
				}
				.t-info{
					margin-bottom: 0.5rem;
					th{
						height: 1.0rem;
						text-align: center;
						color: #fff;
						font-size: 0.45rem;
						border: 1px solid #15224d;
						background: rgba(21, 34, 77, 0.7);
					}
					td{
						text-align: center;
						color: #fff;
						height: 1.0rem;
						border: 1px solid #15224d;
						background: rgba(1, 25, 61, 0.7);
						padding: 0;
					}
				}
			}
			.echarts{
				overflow: hidden;
				.w-echart{
					width: 49.9%;
					height: 6.85rem;
				}
				.echar-l{
					float: left;
				}
				.echar-r{
					float: right;
				}
			}
		}
		.mar-tb30{
			margin: 0.75rem 0;
		}
	}
}
.patient-layer{
	display: none;
	.layer-bg{
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 100;
		background: rgba(0, 0, 0, 0.5);
	}
}
.table-layer{
	width: 36.5rem;
	height: 20.15rem;
	position:absolute;
	top: 2.5rem;
	margin-left: -18.25rem;
	z-index: 200;
	.patient-table{
		padding: 1.7rem 1.1rem 0.5rem;
		table{
			width: 100%;
		}
		th{
			font-size:0.45rem;
			color: #fff;
			height: 1.25rem;
			background:#000521;
			text-align: center;
		}
		.thead{
			border: 2px solid #15538e;
			padding-right:0.85rem
		}
		.tbody{
			padding-right:0.85rem;
			border: 2px solid #15538e;
			border-top: none;
			max-height: 16.2rem;
			overflow: hidden;
			td{
				font-size: 0.4rem;
				color: #fff;
				height: 1.2rem;
				text-align: center;
			}
			.fn-c-r{
				color: #ff4b50;
			}
			.fn-c-y{
				color: #fcdb0f;
			}
			tr:nth-child(2n-1){
				background: #011237;
			}
		}
		.w6{
			width: 6%;
		}
		.w8{
			width: 8%;
		}
		.w17{
			width: 17%;
		}
		.w14{
			width: 14%;
		}
		.w9{
			width: 9%;
		}
		.w18{
			width: 18%;
		}
	}
}